export function injectSourceScript(src: string): void {
  if (!src) {
    return;
  }

  const script = window.document.createElement('script');

  script.type = 'text/javascript';
  script.async = true;
  script.src = src;
  window.document.head.appendChild(script);
}

export function injectInitScript(src: string): void {
  if (!src) {
    return;
  }

  const script = window.document.createElement('script');

  script.type = 'text/javascript';
  script.text = src;
  window.document.head.appendChild(script);
}
